* {
  font-style: 'Montserrat', sans-serif;
}

body {
  padding: 20px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: gray;
  border-radius: 3px;
}

.card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-items: center;
  border: 2px solid gray;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 10px;
}

.card img {
  max-height: 200px;
}

.card p {
  padding: 10px;
  margin: 5px;
}

